	margin-left: 1.5em;
}

dt {
	font-weight: bold;
}

dd {
	margin: 0 1.5em 1.5em;
}

/* TABLES */
table {
	margin: 0 0 1.5em;
	width: 100%;
}

.table__header-sortable {
