}

.indicatr {
	height: 18px;
	width: 18px;
	background: white;
	border-radius: 50%;
	display: block;
	border: 1px solid rgba(87, 87, 87, 0.2);
	padding: 2px;
	margin: 0 auto;
}

.indicatr .responded {
	height: 100%;
	width: 100%;
	background: #11C684;
	border-radius: 50%;
	display: block;
}

.indicatr .unresponded {
	height: 100%;
	width: 100%;
	background: #c61111;
	border-radius: 50%;
	display: block;
}

.switch {
	position: relative;
	display: inline-block;
	width: 40px;
	height: 20px;
	margin: 0 10px;
}

.switch input {
	opacity: 0;
	width: 0;
	height: 0;
}

.switch input:checked + .slider {
	background-color: #4963FE;
}

.switch input:checked + .slider:before {
	transform: translateX(20px);
}

.switch .slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #4A4A4A;
	-webkit-transition: .4s;
	transition: .4s;
}

.switch .slider:before {
	position: absolute;
	content: "";
	height: 16px;
	width: 16px;
	left: 2px;
	bottom: 2px;
	background-color: white;
	-webkit-transition: .4s;
	transition: .4s;
}

.switch .slider.round {
	border-radius: 34px;
}

.switch .slider.round:before {
	border-radius: 50%;
}

.entries__pp .form-group {
