	stroke-width: 10;
	stroke-linecap: round;
	-webkit-filter: drop-shadow(-3px -2px 10px rgba(98, 149, 255, 0.2));
	filter: drop-shadow(-3px -2px 10px rgba(98, 149, 255, 0.2));
}

.progress-bar-donut .progressbar-text {
	color: #4A4A4A;
	font-size: 48px;
	line-height: 1;
	font-weight: bold;
	text-align: center;
	letter-spacing: -0.05em;
}

.progress-bar-donut .progressbar-text span {
	font-size: 32px;
	margin-left: 5px;
}

.progress-bar-donut .progressbar-text .progress-bar-title {
	font-weight: 600;
	font-size: 16px;
	text-align: center;
	margin-bottom: 0;
	letter-spacing: normal;
}

.progress-bar-line {
	position: relative;
	height: 56px;
	background: none;
}

.progress-bar-line svg {
	position: absolute;
	bottom: 0;
	height: 20px;
	width: 100%;
	border-radius: 10px;
	fill: none;
	stroke-linecap: round;
	-webkit-filter: drop-shadow(-3px -2px 10px rgba(98, 149, 255, 0.2));
	filter: drop-shadow(-3px -2px 10px rgba(98, 149, 255, 0.2));
}

.progress-bar-line .progressbar-text {
	position: absolute;
	top: 0 !important;
	left: 0 !important;
	transform: translate(0%, 0%) !important;
	color: #4A4A4A;
	font-size: 18px;
	line-height: 1;
	font-weight: 700;
	text-align: center;
	letter-spacing: -0.05em;
	display: flex;
	width: 100%;
	padding: 0 40px;
}

.progress-bar-line .progressbar-text span {
	margin-left: auto;
}

.progress-bar-line .progressbar-text .progress-bar-title {
	font-weight: 600;
	font-size: 16px;
	text-align: center;
	margin-bottom: 0;
	letter-spacing: normal;
}

.progress-bar-description span {
