/* BUTTONS */
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	position: relative;
	overflow: hidden;
	z-index: 0;
	background-color: rgba(255, 255, 255, 0);
	font-size: 15px;
	font-weight: 600;
	color: #4963FE;
	padding: 8px 32px;
	display: inline-block;
	border-radius: 20px;
	border: 2px solid #4963FE;
	box-shadow: 0 2px 15px 0 rgba(98, 149, 255, 0.2);
	transition: all .3s ease;
	cursor: pointer;
}

button:before,
input[type="button"]:before,
input[type="reset"]:before,
input[type="submit"]:before {
	content: '';
	position: absolute;
	width: 0;
	height: 40px;
	border-radius: 20px;
	top: -1px;
	left: -10%;
	z-index: -1;
	background: #4963FE;
	-moz-transition: all 0.6s cubic-bezier(0.19, 1, 0.22, 1);
	-o-transition: all 0.6s cubic-bezier(0.19, 1, 0.22, 1);
	-webkit-transition: all 0.6s cubic-bezier(0.19, 1, 0.22, 1);
	transition: all 0.6s cubic-bezier(0.19, 1, 0.22, 1);
}

button svg,
input[type="button"] svg,
input[type="reset"] svg,
input[type="submit"] svg {
	margin-top: -3px;
	margin-left: 10px;
}

button .button__icon-blue,
input[type="button"] .button__icon-blue,
input[type="reset"] .button__icon-blue,
input[type="submit"] .button__icon-blue {
	fill: #4963FE;
}

button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
	color: white;
	text-decoration: none;
}

button:hover .button__icon,
input[type="button"]:hover .button__icon,
input[type="reset"]:hover .button__icon,
input[type="submit"]:hover .button__icon {
	fill: white;
}

button:hover:before,
input[type="button"]:hover:before,
input[type="reset"]:hover:before,
input[type="submit"]:hover:before {
	width: 120%;
}

button:disabled,
button[disabled] {
	border: 2px solid #25283D;
	color: #25283D;
	box-shadow: none;
}

button:disabled:hover:before,
button[disabled]:hover:before {
	width: 0;
}

